<!-- 推荐生成结果审核校验及推荐执行任务管理 -->
<template>
  <div class="auditManagement">
    <div class="content">
      <bdaTable
        class="content-table"
        :tableData="taskData"
        :headerColor="taskHeaderColor"
        :indexWidth="0"
        @operate="taskOperate"
        :isPage="true"
        :pageConfig="pageConfig"
        @changePage="changePage"
      ></bdaTable>
    </div>
    <resultPreviewDialog
      v-if="resultDialogVisible"
      :visible.sync="resultDialogVisible"
      :type="resultDialogType"
      :isNeedConfirm="isNeedConfirm"
      :showData="resultDialogData"
    ></resultPreviewDialog>
    <el-dialog
      class="task-dialog"
      title="提示"
      :visible.sync="taskDialogVisible"
      :before-close="taskDialogClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <div slot="title" class="dialog-header">
        <span class="header-text">{{ "任务信息" }}</span>
      </div>
      <div class="dialog-content">
        <el-form class="task-form" :model="taskForm" label-width="160px">
          <el-row>
            <el-col :span="18">
              <el-form-item label="执行方式：" prop="exeType">
                <el-select
                  size="mini"
                  class="input"
                  :disabled="executeCheck"
                  style="width: 100%"
                  v-model="taskForm.exeType"
                  placeholder="请选择"
                >
                  <el-option
                    v-for="item in exeTypeOptions"
                    :key="item.value"
                    :label="item.name"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="18">
              <el-form-item label="执行渠道：" prop="exeChannel">
                <el-select
                  size="mini"
                  class="input"
                  :disabled="executeCheck"
                  style="width: 100%"
                  v-model="taskForm.exeChannel"
                  placeholder="请选择"
                >
                  <el-option
                    v-for="item in exeChannelOptions"
                    :key="item.value"
                    :label="item.name"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button v-if="!executeCheck" type="primary" size="mini" @click="submitApproval">提交审批</el-button>
        <el-button v-else @click="taskDialogClose" size="mini">
          {{
          "确定"
          }}
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Interface from '@/base/mixin/recommend-management';
import bdaTable from "@/components/bda-table/bda-table";
import resultPreviewDialog from "./components/result-preview-dialog";

export default {
  components: {
    bdaTable,
    resultPreviewDialog
  },
  name: "auditManagement",
  mixins: [Interface],
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    }
  },
  props: {
    menuType: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      //推荐生成任务
      taskData: {},
      taskHeaderColor: "",
      queryForm: {
        current: 1,
        size: 10,
        total: 0
      },
      //分页设置
      pageConfig: {
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //执行结果查看
      resultDialogVisible: false,
      resultDialogType: "",
      resultDialogData: {},
      //任务
      taskDialogVisible: false,
      taskForm: {
        relationNumber: "",
        exeType: "",
        exeChannel: ""
      },
      //执行方式选项
      exeTypeOptions: [],
      //执行渠道选项
      exeChannelOptions: [],
      //是否查看任务详细信息
      executeCheck: false,
      //是否需要校验结果
      isNeedConfirm: false,
    };
  },
  mounted() {
    this.getTaskData();
    this.getexeTypeOptions();
    this.getexeChannelOptions();
  },
  methods: {
    //点击任务表格
    taskOperate(type, data) {
      console.log(type, data);
      if (type === "histroy") {
        this.resultDialogType = "histroy";
        this.resultDialogData = data
        this.isNeedConfirm = false
        this.resultDialogVisible = true;
      }
      if (type === "executeResult") {
        this.resultDialogType = "executeResult";
        this.resultDialogData = data
        this.isNeedConfirm = true
        this.resultDialogVisible = true;
      }
      if (type === "set") {
        this.taskForm.relationNumber = data.relationNumber
        this.executeCheck = false
        // this.checkTaskDetail()
        this.taskForm.exeType = ''
        this.taskForm.exeChannel = ''
        this.taskDialogVisible = true;
      }
      if (type === "detail") {
        this.executeCheck = true
        this.taskForm.relationNumber = data.relationNumber
        this.checkTaskDetail()
      }
    },
    //关闭任务模态框
    taskDialogClose() {
      this.taskDialogVisible = false;
    },
    //提交审批
    submitApproval() {
      this.saveTaskVerification(this.taskForm).then(res => {
        this.$message.success('提交成功')
        this.taskDialogVisible = false;
      })
    },
    //查看任务详情信息
    checkTaskDetail() {
      this.getTaskDetailInfo({ relationNumber: this.taskForm.relationNumber }).then(res => {
        this.taskForm.exeType = res.exeType
        this.taskForm.exeChannel = res.exeChannel
        this.taskDialogVisible = true;
      })
    },
    //改变分页
    changePage(data) {
      this.queryForm.size = data.size
      this.queryForm.current = data.current
      this.getTaskData()
    },
    //获取推荐生成任务列表
    getTaskData() {
      this.taskHeaderColor = "#1899fa";
      let columnNameData = [
        { prop: "relationName", label: "关联关系名称" },
        { prop: "updateTime", label: "任务更新时间" },
        {
          prop: "Operate",
          label: "历史匹配查看",
          align: "center",
          width: "140px",
          type: [{ name: "详细结果", action: "histroy" }]
        },
        { prop: "executeEndTime", label: "执行完成时间" },
        { prop: "checkState", label: "执行申请状态", width: "140px" },
        {
          prop: "Operate",
          label: "操作",
          align: "center",
          width: "310px",
          type: [
            { name: "查看执行结果", action: "executeResult" },
            {
              name: "任务执行配置",
              action: "set",
              // disabledField: "executionStatus",
              // disabledValue: ["01", "02"]
            },
            {
              name: "任务详细信息",
              action: "detail",
              // disabledField: "executionStatus",
              // disabledValue: ["01"]
            }
          ]
        }
      ]
      this.getTaskVerification({ ...this.queryForm, menuType: this.menuType }).then(res => {
        this.pageConfig.total = res.total
        this.pageConfig.size = this.queryForm.size
        this.pageConfig.current = this.queryForm.current
        this.taskData = {
          tableData: [...res.list],
          columnNameData: columnNameData
        }
      }).catch((err) => {
        if (err) {
          this.taskData = {
            tableData: [],
            columnNameData: columnNameData
          }
        }
      })
    },
    //获取执行方式选项
    getexeTypeOptions() {
      this.exeTypeOptions = [
        { name: "自动执行", value: "1" },
        { name: "手工执行", value: "0" }
      ];
    },
    //获取执行渠道选项
    getexeChannelOptions() {
      this.exeChannelOptions = [
        { name: "智能IVR", value: "智能IVR" },
        { name: "语音平台", value: "语音平台" },
        { name: "短信平台", value: "短信平台" }
      ];
    }
  }
};
</script>

<style scoped lang="scss">
.auditManagement {
  width: 100%;
  height: 100%;
  .content {
    ::v-deep .el-table .warning-row {
      background: #e9f3fe;
    }
    width: 100%;
    height: calc(100%);
    background: #ffffff;
    padding: 10px;
    box-sizing: border-box;

    .content-table {
      height: 100%;
    }
  }
  .task-dialog {
    ::v-deep .el-dialog__header {
      padding: 10px 0 10px;
      border-bottom: 1px solid #e6e6e6;
    }
    ::v-deep .el-dialog__body {
      padding: 10px;
    }
    ::v-deep .el-dialog__footer {
      padding: 10px 10px 10px 0;
      border-top: 1px solid #e6e6e6;
      text-align: center;
    }
    .dialog-header {
      .header-text {
        margin-left: 20px;
        color: #808080;
        font-size: 14px;
      }
    }
    .dialog-content {
      .input {
        width: 80%;
      }
    }
  }
}
</style>
