<template>
  <div class="result-preview-dialog">
    <el-dialog
      class="dialog"
      title="提示"
      v-if="this.visible"
      :visible.sync="this.visible"
      width="70%"
      :before-close="handleClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <span slot="title" class="dialog-header">
        <el-row style="display: flex; align-items: center">
          <el-col :span="4">
            <span class="header-text">{{ isPreview?"推荐结果预览":"推荐关联关系" }}</span>
          </el-col>
          <el-col :span="9" v-if="type === 'histroy'" style="display: flex; align-items: center">
            <span style="width: 100px">更新时间：</span>
            <el-date-picker
              v-model="time"
              type="date"
              size="mini"
              value-format="yyyy-MM-dd"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            ></el-date-picker>
          </el-col>
        </el-row>
      </span>
      <div class="dialog-content">
        <div class="dialog-content-top" v-if="!isPreview">
          <!-- <div class="dialog-title">
            <span class="text">推荐关联关系</span>
          </div>-->
          <!-- <div class="dialog-search">
            <el-form class="dialog-form" :model="labelForm" label-width="140px">
              <el-row>
                <el-col :span="9">
                  <el-form-item label="目标对象标签：" prop="targetSubjectField">
                    <el-select
                      size="mini"
                      class="input"
                      v-model="labelForm.targetSubjectField"
                      placeholder="请选择"
                    >
                      <el-option
                        v-for="item in targetSubjectLabelOptions"
                        :key="item.value"
                        :label="item.name"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="9">
                  <el-form-item label="推荐对象标签：" prop="recommendedSubjectField">
                    <el-select
                      size="mini"
                      class="input"
                      v-model="labelForm.recommendedSubjectField"
                      placeholder="请选择"
                    >
                      <el-option
                        v-for="item in recommendedSubjectLabelOptions"
                        :key="item.value"
                        :label="item.name"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="6">
                  <el-form-item label label-width="0px">
                    <el-button type="primary" size="mini" @click="searchLabelRelationData">搜索</el-button>
                  </el-form-item>
                </el-col>
              </el-row>
            </el-form>
          </div>-->
          <div class="dialog-table">
            <el-table
              v-if="labelRelationRes"
              :data="labelRelationData"
              class="dialog-table"
              :height="'187px'"
              border
              :header-cell-style="headStyle"
              style="width: 100%; height: 100%"
            >
              <el-table-column type="index" label="关联序号" :width="'100px'"></el-table-column>
              <el-table-column prop="tagTargetName" label="目标对象名称"></el-table-column>
              <el-table-column prop="tagRecName" label="推荐对象名称"></el-table-column>
              <el-table-column prop="weight" label="权重" width="200"></el-table-column>
            </el-table>
            <div v-else class="no-data">
              <span>暂无数据</span>
            </div>
          </div>
          <el-pagination
            class="dialog-pagination"
            @size-change="labelSizeChange"
            @current-change="labelCurrentChange"
            :current-page="labelForm.current"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="labelForm.size"
            layout=" prev, pager, next, sizes,jumper,total"
            :total="labelForm.total"
          ></el-pagination>
        </div>
        <div class="dialog-content-bottom" v-loading="loading">
          <div class="dialog-title" v-if="!isPreview">
            <span class="text">推荐结果预览</span>
          </div>
          <div class="dialog-search">
            <el-form class="dialog-form" :model="resultForm" label-width="160px">
              <el-row>
                <el-col :span="9">
                  <el-form-item label="目标对象显示字段：" prop="targetSubjectField">
                    <el-select
                      size="mini"
                      class="input"
                      multiple
                      v-model="resultForm.targetSubjectField"
                      placeholder="请选择"
                      collapse-tags
                    >
                      <el-option
                        v-for="item in targetSubjectFieldOptions"
                        :key="item.value"
                        :label="item.name"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="9">
                  <el-form-item label="推荐对象显示字段：" prop="recommendedSubjectField">
                    <el-select
                      size="mini"
                      class="input"
                      multiple
                      v-model="resultForm.recommendedSubjectField"
                      placeholder="请选择"
                      collapse-tags
                    >
                      <el-option
                        v-for="item in recommendedSubjectFieldOptions"
                        :key="item.value"
                        :label="item.name"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="9">
                  <el-form-item
                    :label="this.filterField(this.resultForm.targetKeyName)+'：'"
                    prop="targetSearchText"
                  >
                    <el-input
                      size="mini"
                      class="input"
                      :placeholder="'请输入'"
                      v-model="resultForm.targetSearchText"
                    ></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="9">
                  <el-form-item
                    :label="this.filterField(this.resultForm.recKeyName)+'：'"
                    prop="recommendSearchText"
                  >
                    <el-input
                      size="mini"
                      class="input"
                      :placeholder="'请输入'"
                      v-model="resultForm.recommendSearchText"
                    ></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="6">
                  <el-form-item label label-width="0px">
                    <el-button type="primary" size="mini" @click="searchResultPreviewData">搜索</el-button>
                  </el-form-item>
                </el-col>
              </el-row>
            </el-form>
          </div>
          <div class="dialog-table" :style="{ height: isPreview ? '345px' : '187px' }">
            <el-table
              v-if="resultPreviewRes"
              :data="resultPreviewData"
              border
              :height="isPreview ? '342px' : '187px'"
              :header-cell-style="headStyle"
              :cell-style="timeStyle"
              @selection-change="handleSelectionChange"
            >
              <el-table-column
                v-if="isSelect&&resultPreviewData.length!==0"
                type="selection"
                width="55"
              ></el-table-column>
              <el-table-column
                v-for="(item, index) in columnData"
                :key="index"
                :prop="item.prop"
                :label="item.label"
                :width="item.width"
                :align="item.align ? item.align : 'left'"
              >
                >
                <template slot-scope="scope">
                  <div v-if="item.prop === 'Operate'" class="operate">
                    <el-button
                      type="text"
                      v-for="(item1, index1) in item.type"
                      :key="index1"
                      @click="operate(item1.action, scope.row)"
                    >{{ item1.name }}</el-button>
                  </div>
                  <div v-else class="text">
                    <span>{{ scope.row[item.prop] }}</span>
                  </div>
                </template>
              </el-table-column>
            </el-table>
          </div>

          <el-pagination
            class="dialog-pagination"
            @size-change="resultSizeChange"
            @current-change="resultCurrentChange"
            :current-page="resultForm.current"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="resultForm.size"
            layout=" prev, pager, next, sizes,jumper,total"
            :total="resultForm.total"
          ></el-pagination>
        </div>
      </div>

      <span slot="footer" class="dialog-footer">
        <el-button v-if="type==='executeEntry'" type="primary" @click="entry">录入</el-button>
        <el-button v-if="isNeedConfirm" type="primary" @click="comfirm">校验通过</el-button>
        <el-button @click="handleClose">关闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
// import Interface from '../base/mixin/talk-manage';
import Interface from '@/base/mixin/recommend-management';
export default {
  components: {},
  mixins: [Interface],
  props: {
    visible: {
      type: Boolean,
      default: () => {
        return false;
      }
    },
    showData: {
      type: Object,
      default: () => {
        return {};
      }
    },
    type: {
      type: String,
      default: () => {
        return "";
      }
    },
    isNeedConfirm: {
      type: Boolean,
      default: () => {
        return false;
      }
    },
    isSelect: {
      type: Boolean,
      default: () => {
        return false;
      }
    }
  },
  //   mixins: [Interface],

  data() {
    return {
      //是否监督
      isSupervision: false,
      //是否录入
      isEntry: false,
      //是否结果预览
      isPreview: false,
      time: '',
      loading: true,
      //查询数据
      queryData: {},
      //弹出框
      dialogRes: false,
      dialogHeaderColor: "",
      resultForm: {
        targetSubjectField: "",
        recommendedSubjectField: "",
        targetSearchText: "",
        recommendSearchText: "",
        targetKey: "",
        recKey: "",
        targetKeyName: "",
        recKeyName: "",
        current: 1,
        size: 10,
        total: 500
      },
      //多选数据
      selectionData: [],
      //表头数据
      columnData: [],
      //表头颜色
      headStyle: { background: "#189afb" },
      //目标主体的字段
      targetSubjectFieldOptions: [],
      //推荐主体的字段
      recommendedSubjectFieldOptions: [],
      //表格数据
      resultPreviewData: [],
      resultPreviewRes: true,
      //标签表单
      labelForm: {
        // targetSubjectLabel: "",
        // recommendedSubjectLabel: "",
        current: 1,
        size: 10,
        total: 0
      },
      //目标主体的标签
      targetSubjectLabelOptions: [],
      //推荐主体的标签
      recommendedSubjectLabelOptions: [],
      //表格数据
      labelRelationData: [],
      labelRelationRes: false,

    };
  },
  watch: {
    type() {
      this.typeDeal();
    }
  },
  mounted() {
    this.typeDeal();
    this.getTargetSubjectLabelOptions();
    this.getRecommendedSubjectLabelOptions();

  },
  methods: {
    timeStyle(column) {
      if (
        column.columnIndex < this.columnData.filter(e => e.differColor).length
      ) {
        return "background-color: #e3f7f3";
      } else {
        return "background-color: #fffaeb";
      }
    },
    // 类型数据处理
    typeDeal() {
      console.log(this.type, "this.type");
      if (this.type === 'executeEntry' || this.type === 'executeResultShow') {
        this.isEntry = true;
      }
      if (this.type === 'executeSupervision') {
        this.isSupervision = true;
      }
      if (this.type === "preview") {
        this.isPreview = true;
        this.dataInitPreview()
      } else {
        this.isPreview = false;
        this.dataInitResult()
        this.getLabelRelationData();
      }
    },
    //数据处理预览
    dataInitPreview() {
      let data = {
        ...this.showData,
        current: this.resultForm.current,
        size: this.resultForm.size,
      };
      this.queryData = { ...data }

      this.resultPreview({
        current: data.current,
        size: data.size,
        recId: data.recId,
        targetId: data.targetId,
        recTagId: data.listRelationPair[0].tagRecId,
        targetTagId: data.listRelationPair[0].tagTargetId,
      }
      ).then(res => {
        this.resultForm.total = res.page.total
        this.getTargetSubjectFieldOptions(res.showField[1])
        this.getRecommendedSubjectFieldOptions(res.showField[0])
        console.log(this.columnData, 'dbcolumnName')

        this.resultPreviewData = [
          ...res.result
        ]
        this.loading = false
      }).catch(err => {
        if (err) {
          this.loading = false
        }
      })


    },
    //数据处理结果
    dataInitResult() {
      console.log(this.showData, 'this.showData')
      let data = {
        ...this.showData,
        current: this.resultForm.current,
        size: this.resultForm.size,
      };
      this.queryData = { ...data }
      let params = {
        current: data.current,
        size: data.size,
        relationNumber: data.relationNumber,
        exeType: this.isEntry ? data.exeType : "",
        version: this.time,
        callType: this.isSupervision ? '2' : '1'
      }

      this.resultCrate(params).then(res => {
        this.resultForm.total = res.page.total
        this.getTargetSubjectFieldOptions(res.showField[1])
        this.getRecommendedSubjectFieldOptions(res.showField[0])
        console.log(this.columnData, 'dbcolumnName')
        if (this.isEntry) {
          this.columnData.push(
            {
              prop: 'IS_REDER',
              label: "是否被读取",
              differColor: false
            },
            {
              prop: 'EXE_TIME',
              label: "执行时间",
              differColor: false
            },
          )
        }
        if (this.isSupervision) {
          this.columnData.push(
            {
              //requestNum
              prop: 'REQUEST_NUM',
              label: "读取次数",
              differColor: false
            },
            {
              //useNum
              prop: 'USE_NUM',
              label: "来电次数",
              differColor: false
            },
          )
        }

        this.resultPreviewData = [
          ...res.result
        ]
        this.loading = false
      }).catch(err => {
        if (err) {
          this.loading = false
        }
      })

    },
    //点击搜索
    searchResultPreviewData() {
      // this.getPreviewData();
      if (this.isPreview) {
        this.getPreviewData()
      } else {
        this.getResultData()
      }
    },
    //推荐结果表多选
    handleSelectionChange(val) {
      this.selectionData = val
    },
    //查询标签关联数据
    searchLabelRelationData() {

    },
    //条数改变
    resultSizeChange(val) {
      this.resultForm.size = val;
      this.isPreview ? this.getPreviewData() : this.getResultData()
    },
    //页数改变
    resultCurrentChange(val) {
      this.resultForm.current = val;
      this.isPreview ? this.getPreviewData() : this.getResultData()

    },
    //条数改变
    labelSizeChange(val) {
      this.labelForm.size = val;
      this.getLabelRelationData();

    },
    //页数改变
    labelCurrentChange(val) {
      this.labelForm.current = val;
      this.getLabelRelationData();

    },
    //录入
    entry() {
      let data = this.selectionData.map(e => {
        return {
          targetValue: e[this.resultForm.targetKeyName],
          recValue: e[this.resultForm.recKeyName],
        }
      })
      this.resultReadUpdate({ previewRequestList: data }).then(res => {
        this.$message.info(res)
      })
    },
    //校验通过
    comfirm() {
      this.dialogRes = false;
      this.updateCheckState({
        relationNumber: this.queryData.relationNumber,
        checkState: this.queryData.checkStateId,
      }).then(res => {
        this.$message.success('校验完成')
        this.$emit("update:visible", false);
      }).catch(err => {
        if (err) {
          this.$emit("update:visible", false);
        }
      })
    },
    //关闭对话框
    handleClose() {
      this.dialogRes = false;
      //   this.dialogVisible = false
      this.$emit("update:visible", false);
    },



    //获取推荐结果
    getPreviewData() {
      this.resultPreviewRes = false;
      this.loading = true

      this.resultPreview(
        {
          current: this.resultForm.current,
          size: this.resultForm.size,
          recId: this.queryData.recId,
          targetId: this.queryData.targetId,
          recTagId: this.queryData.listRelationPair[0].tagRecId,
          targetTagId: this.queryData.listRelationPair[0].tagTargetId,
          targetKey: this.resultForm.targetKey,
          recKey: this.resultForm.recKey,
          recValue: this.resultForm.recommendSearchText,
          targetValue: this.resultForm.targetSearchText
        }
      ).then(res => {
        console.log(res)
        // // console.log(res)
        this.resultForm.total = res.page.total
        console.log(this.resultForm.targetSubjectField, 'targetSubjectField')
        console.log(this.resultForm.recommendedSubjectField, 'recommendedSubjectField')
        let columnData = []
        this.resultForm.targetSubjectField.forEach(e => {
          columnData.push(
            {
              prop: e,
              label: this.filterField(e),
              differColor: true
            }
          )
        }
        );
        this.resultForm.recommendedSubjectField.forEach(e => {
          columnData.push(
            {
              prop: e,
              label: this.filterField(e),
            }
          )
        });

        this.columnData = columnData
        this.resultPreviewData = [
          ...res.result
        ]
        this.loading = false

        this.resultPreviewRes = true;
      }).catch(err => {
        if (err) {
          this.loading = false
          this.resultPreviewRes = true;
        }
      })



    },
    //获取推荐结果
    getResultData() {
      this.resultPreviewRes = false;
      this.loading = true
      let params = {
        current: this.resultForm.current,
        size: this.resultForm.size,
        relationNumber: this.queryData.relationNumber,
        targetKey: this.resultForm.targetKey,
        recKey: this.resultForm.recKey,
        recValue: this.resultForm.recommendSearchText,
        targetValue: this.resultForm.targetSearchText,
        version: this.time,
        exeType: this.isEntry ? this.queryData.exeType : "",
        callType: this.isSupervision ? '2' : '1'
      }

      this.resultCrate(params).then(res => {
        // console.log(res)
        // console.log(res)
        this.resultForm.total = res.page.total
        console.log(this.resultForm.targetSubjectField, 'targetSubjectField')
        console.log(this.resultForm.recommendedSubjectField, 'recommendedSubjectField')
        let columnData = []
        this.resultForm.targetSubjectField.forEach(e => {
          columnData.push(
            {
              prop: e,
              label: this.filterField(e),
              differColor: true
            }
          )
        }
        );
        this.resultForm.recommendedSubjectField.forEach(e => {
          columnData.push(
            {
              prop: e,
              label: this.filterField(e),
            }
          )
        });

        this.columnData = columnData
        this.resultPreviewData = [
          ...res.result
        ]
        this.loading = false

        this.resultPreviewRes = true;
      }).catch(err => {
        if (err) {
          this.loading = false
          this.resultPreviewRes = true;
        }
      })
    },
    //返回字段名
    filterField(value) {
      let options = [
        ...this.targetSubjectFieldOptions,
        ...this.recommendedSubjectFieldOptions,
      ]
      let name = ''
      options.forEach(e => {
        if (e.value === value) {
          name = e.name
        }
      })
      return name
    },
    //获取目标对象字段
    getTargetSubjectFieldOptions(data) {
      this.targetSubjectFieldOptions = data.map(e => {
        if (e.isKey) {
          this.resultForm.targetKey = e.dbcolumnName
          this.resultForm.targetKeyName = e.dbcolumnAS
        }
        return {
          name: e.dbcolumnTitle,
          value: e.dbcolumnAS,
        }

      })

      this.resultForm.targetSubjectField.push(this.resultForm.targetKeyName)

      this.columnData.push(
        {
          prop: this.resultForm.targetKeyName,
          label: this.filterField(this.resultForm.targetKeyName),
          differColor: true
        }
      )
    },
    //获取推荐对象字段
    getRecommendedSubjectFieldOptions(data) {

      this.recommendedSubjectFieldOptions = data.map(e => {
        if (e.isKey) {
          this.resultForm.recKey = e.dbcolumnName
          this.resultForm.recKeyName = e.dbcolumnAS

        }
        return {
          name: e.dbcolumnTitle,
          value: e.dbcolumnAS,
        }
      })
      this.resultForm.recommendedSubjectField.push(this.resultForm.recKeyName)

      this.columnData.push(
        {
          prop: this.resultForm.recKeyName,
          label: this.filterField(this.resultForm.recKeyName),
          differColor: false
        }
      )

    },
    //获取目标主体标签选项
    getTargetSubjectLabelOptions() {
      this.targetSubjectLabelOptions = [
        { name: "经常投诉客户", value: "001" },
        { name: "经常点赞客户", value: "002" }
      ];
    },
    //获取推荐主体标签选项
    getRecommendedSubjectLabelOptions() {
      this.recommendedSubjectLabelOptions = [
        { name: "擅长处理投诉客户的坐席", value: "001" },
        { name: "不擅长处理投诉客户的坐席", value: "002" }
      ];
    },
    //获取标签关联
    getLabelRelationData() {
      console.log(this.queryData, 'this.queryData')
      this.labelRelationRes = false;
      this.recommendationCorrelation({ tagId: this.queryData.tagId, ...this.labelForm }).then(res => {
        this.labelForm.total = res.total
        this.labelRelationData = [...res.records]
        this.labelRelationRes = true;

      })
    }
  }
};
</script>

<style scoped lang="scss">
.result-preview-dialog {
  .dialog {
    ::v-deep .el-table .warning-row {
      background: #ecf5fd;
    }
    ::v-deep .el-table tr th .cell {
      color: #fff;
    }
    ::v-deep .el-dialog__header {
      padding: 10px 0 10px;
      border-bottom: 1px solid #e6e6e6;
    }
    ::v-deep .el-dialog__body {
      padding: 10px;
    }
    ::v-deep .el-dialog__footer {
      padding: 10px 10px 10px 0;
      border-top: 1px solid #e6e6e6;
      text-align: center;
    }
    ::v-deep .el-pagination__total {
      margin-left: 10px;
    }
    .dialog-header {
      .header-text {
        margin-left: 20px;
        color: #808080;
        font-size: 14px;
      }
    }
    .dialog-content {
      .dialog-search {
        .input {
          width: 90%;
        }
      }
      .dialog-table {
        height: 187px;
      }
      .no-data {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #909399;
      }
      .dialog-pagination {
        text-align: center;
        margin: 10px 0;
      }
      .dialog-content-bottom {
        .dialog-title {
          padding: 10px 0;
          border-bottom: 1px solid #e6e6e6;
          color: #808080;
          .text {
            margin-left: 10px;
          }
        }
      }
    }
  }
}
</style>
